<template>
  <div class="list">
    <table class="list-table">
      <tr>
        <th>序号</th>
        <th>名称</th>
        <th>数量</th>
        <th>更新时间</th>
        <th>操作</th>
      </tr>
      <tr v-if="!listData.length">
        <td class="no-data" colspan="5">暂无数据</td>
      </tr>
      <tr v-if="listData.length" v-for="(item,index) in listData" :key="index">
        <td>{{index+1}}</td>
        <td>{{item.name}}</td>
        <td>{{item.num}}</td>
        <td>{{item.time | formatDate}}</td>
        <td>
          <button class="btn btn-add" @click="detail(index)">详情</button>
        </td>
      </tr>
    </table>
  </div>
</template>
<script>
export default {
  created () {
    this.$store.dispatch('getFruitData')
  },
  computed: {
    listData() {
      return this.$store.state.fruitData
    }
  },
  methods: {
    detail(index) {
      this.$router.push(`/detail?id=${index}`)
    }
  }
}
</script>

<style lang="less" scoped>
//表格
.list-table {
  margin: 25px auto 15px;
  width: 100%;
  border-collapse: collapse;
  th {
    background: #bbbec4;
    font-size: 16px;
    text-align: center;
    line-height: 30px;
    color: #fff;
    border: 1px solid #bbbec4;
  }
  td {
    border: 1px solid #bbbec4;
    padding: 5px;
    text-align: center;
    font-size: 16px;
    color: #282b32;
  }
  .no-data{
    color: #DDDEE1;
    padding: 10px 0;
  }
}
</style>



